import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
import ReviewDetails from '@/view/common/review-details.vue';// 鲸闻评论详情

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/review-album.html',
  routes: [
    {
      path: '/',
      name: 'ReviewDetails',
      component: ReviewDetails
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


